import React, { useEffect } from "react";
export const HousePick = (props) => {
  const [show, setShow] = React.useState(false);
  useEffect(() => {
    setTimeout(() => {
      setShow(true);
    }, 2000);
  }, [props.p]);
  console.log("H", props.iswinner);
  return (
    <>
      <div
        className={`${
          !props.iswinner ? "btn-outline-get winner" : "btn-outline-get"
        }`}
        id={props.p.name}
      >
        {show ? (
          <div className="btn-inner-get">
            <img src={props.p.img} alt="" />
          </div>
        ) : (
          <div className="btn-inner-get">5</div>
        )}
      </div>
    </>
  );
};
